import styled from "styled-components";
import ActionsButton from "./ActionsButton";
import LabelInput from "./LabelInput";

import axios from "axios";
import { useState, useEffect, useCallback } from "react";

interface Props {
    mode: "login" | "register";
}

const authDescriptions = {
    login: {
        usernamePlaceholder: "로그인할 아이디",
        userpassPlaceholder: "로그인할 비밀번호",
        ButtonText: "로그인",
    },
    register: {
        usernamePlaceholder: "가입할 아이디",
        userpassPlaceholder: "가입할 비밀번호",
        ButtonText: "회원가입",
    },
} as const;

const AuthForm = ({ mode }: Props) => {
    const { usernamePlaceholder, userpassPlaceholder, ButtonText } =
        authDescriptions[mode];

    const [userId, setUserId] = useState("");
    const [userPw, setUserPw] = useState("");

    const onuserIdInput = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (mode === "register") {
            setUserId(e.target.value);
        }
    };
    const onuserPwInput = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (mode === "register") {
            setUserPw(e.target.value);
        }
    };

    const onSubmit = useCallback(
        async (e: React.FormEvent<HTMLFormElement>) => {
            e.preventDefault();
            if (mode === "register") {
                try {
                    await axios.post("http://localhost:7070/register", {
                        userid: userId,
                        password: userPw,
                    });
                    setUserId("");
                    setUserPw("");
                    alert("가입완료");
                } catch (error) {
                    console.error(error);
                }
            }
        },
        [userId, userPw, mode],
    );
    return (
        <FormStyled onSubmit={onSubmit}>
            <LabelInput
                label="아이디"
                placeholder={usernamePlaceholder}
                onChange={onuserIdInput}
                value={userId}
            />
            <LabelInput
                type="password"
                label="비밀번호"
                placeholder={userpassPlaceholder}
                onChange={onuserPwInput}
                value={userPw}
            />
            <ActionsButton title={ButtonText} />
        </FormStyled>
    );
};

export default AuthForm;

const FormStyled = styled.form``;
